* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Barlow", sans-serif;
}

.features {
  display: grid;
}
.features .left-col {
  display: flex;
  border: 5px solid green;
  flex-direction: row-reverse;
}
.features .left-col .left-col_text .title {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 3.2rem;
  text-align: center;
  color: hsl(211deg, 28%, 19%);
}
.features .left-col .left-col_text .description {
  text-align: left;
  line-height: 3rem;
  font-size: 1.76rem;
  margin: 3.2rem 0 4rem 0;
  color: #818498;
}
.features .left-col .left-col_text .button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.features .left-col .left-col_text .button-wrapper a {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 1.7rem;
  text-transform: uppercase;
  text-decoration: none;
  color: hsl(211deg, 28%, 19%);
  padding: 0 0.7rem;
}
.features .left-col .left-col_text .button-wrapper .border_yellow {
  position: relative;
  width: 150px;
  height: 0.7rem;
  transform: translateY(-0.6rem);
  z-index: -1;
  border-radius: 0.5rem;
  opacity: 0.5;
  transition: all 0.5s ease;
}
.features .left-col .left-col_text .button-wrapper .yellow {
  background-color: hsl(51deg, 100%, 49%);
}
.features .left-col .left-col_text .button-wrapper:hover .border_yellow {
  opacity: 1;
}
.features .right-col {
  display: flex;
  border: 1px solid blue;
}
.features .right-col .right-col_text .title {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 3.2rem;
  text-align: center;
  color: hsl(211deg, 28%, 19%);
}
.features .right-col .right-col_text .description {
  text-align: left;
  line-height: 3rem;
  font-size: 1.76rem;
  margin: 3.2rem 0 4rem 0;
  color: #818498;
}
.features .right-col .right-col_text .button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.features .right-col .right-col_text .button-wrapper a {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 1.7rem;
  text-transform: uppercase;
  text-decoration: none;
  color: hsl(211deg, 28%, 19%);
  padding: 0 0.7rem;
}
.features .right-col .right-col_text .button-wrapper .border_yellow {
  position: relative;
  width: 150px;
  height: 0.7rem;
  transform: translateY(-0.6rem);
  z-index: -1;
  border-radius: 0.5rem;
  opacity: 0.5;
  transition: all 0.5s ease;
}
.features .right-col .right-col_text .button-wrapper .yellow {
  background-color: hsl(51deg, 100%, 49%);
}
.features .right-col .right-col_text .button-wrapper:hover .border_yellow {
  opacity: 1;
}
.features .bottom-col {
  display: flex;
}
.features .bottom-col .left-col {
  position: relative;
}
.features .bottom-col .left-col .left-col-text {
  position: absolute;
  z-index: 1;
  top: 66%;
  padding: 0 19rem;
}
.features .bottom-col .left-col .left-col-text .title {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 2.8rem;
  text-align: center;
}
.features .bottom-col .left-col .left-col-text .description {
  font-size: 1.55rem;
  text-align: center;
  line-height: 1.7rem;
  margin: 0.8rem 0;
}

.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: rgb(255, 0, 170);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-size: 2rem;
}
.navbar .logo {
  width: 17rem;
}
.navbar .menu {
  display: flex;
  flex-direction: row;
}
.navbar .menu .navbar-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5.1rem;
  list-style: none;
}
.navbar .menu .navbar-nav .nav-item .nav-link {
  font-size: 2rem;
  text-decoration: none;
  color: #fff;
}
.navbar .menu .btn {
  font-family: "Fraunces", serif;
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  background-color: white;
  border-radius: 3rem;
  margin-left: 5rem;
  cursor: pointer;
  color: #23303e;
}

.client-testimonials {
  padding: 6.4rem 2rem 2rem;
}
.client-testimonials h2 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 1.5rem;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}
.client-testimonials .clients-wrapper {
  padding: 3rem 0;
}
.client-testimonials .clients-wrapper .client {
  margin: 0.6rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2.4rem 0.2rem;
}
.client-testimonials .clients-wrapper .client img {
  width: 22%;
  border-radius: 50%;
}
.client-testimonials .clients-wrapper .client .testimonial {
  font-size: 1.8rem;
  text-align: center;
  padding: 3.4rem 0 3.1rem 0;
  line-height: 3.2rem;
  color: hsl(232deg, 10%, 55%);
}
.client-testimonials .clients-wrapper .client .name {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 1.8rem;
  color: hsl(211deg, 28%, 19%);
}
.client-testimonials .clients-wrapper .client .position {
  font-size: 1.4rem;
  color: hsl(210deg, 4%, 67%);
  padding: 1.2rem 0;
}

@media screen and (min-width: 1440px) {
  .client-testimonials {
    padding: 16rem 2rem 7rem;
  }
  .client-testimonials h2 {
    font-size: 2rem;
  }
  .client-testimonials .clients-wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 3rem;
  }
  .client-testimonials .clients-wrapper .client {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: stretch;
    width: 25.5%;
    padding: 4rem 0;
  }
  .client-testimonials .clients-wrapper .client .testimonial {
    padding: 5.4rem 0 7rem 0;
  }
}
.hero-section {
  position: relative;
}
.hero-section .img-desktop {
  width: 100%;
}
.hero-section .hero-text {
  position: absolute;
  top: 27%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.hero-section .hero-text h1 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 4rem;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.7rem;
}
.hero-section .hero-text img {
  margin-top: 5rem;
}